import Row from './Row'
import Col from './Col'

const Grid = {
  Row,
  Col
}

// 网格布局 API
// Row (行)

// Col (列) 元素所占用12格中的多少格
// xs: <  576px or 适配所有屏幕宽度
// sm: >= 576px
// md: >= 768px
// lg: >= 992px
// xl: >= 1200px
// gutter: left and right padding (default 15px)

export default Grid



